/*管理画面のSCSS*/
.wp-block-create-block-pfmodal{
	width: 100%;
    border-radius: 2px;
    box-shadow: inset 0 0 0 1px #1e1e1e;
    box-sizing: border-box;
    color: #1e1e1e;
    margin: 0;
    outline: 1px solid transparent;
    padding: 1em;
    position: relative;
    text-align: left;
}


/*ブロックパターンのプレビュー画像登録*/
div[aria-label="modal1"] {
    background-image: url('./../../../uploads/top.jpg');
}

div[aria-label="modal2"] {
    background-image: url('./../../../uploads/top.jpg');
}


/*ブロックパターン：モーダル画面とそれ以外で表示カテゴリ分岐*/
.block-editor-inserter__patterns-category[aria-label="modal"]{
    display: none;
   }
   
.post-type-modal .block-editor-inserter__patterns-category{
    display: none;
}
   
.post-type-modal .block-editor-inserter__patterns-category[aria-label="modal"]{
    display: block;
}